<template>
  <div class="album_container"   @showIdxPage="showIdxPage">
    <div class="top_container">
        <!-- 头部箭头 -->
      <div class="top_bar">
        <van-icon name="arrow-left" size="25px" @click="goback"  color="#fff" />
      </div>
      <!-- top info -->
      <div class="top_info_Box"  :data='detailtoplist'>
        <div class="left-img"  >
            <img :src="detailtoplist.coverImgUrl" alt="">
            <!-- 播放次数 -->
            <div class="top-icon">
                <van-icon name="play" color="#fff" />
            <span class="count">{{detailtoplist.playCount|| '555' | strdata}}亿</span>
            </div>
        </div>
       

        <!-- right-box -->
        <div class="right-box">
          <div class="title-top">{{detailtoplist.name}}</div>
          <div class="middle_box">
              <van-icon name="audio"  size="25px"/>
              <span class="middle-text">网易云音乐  ></span>
          </div>
          <div class="bottm-text">
              {{detailtoplist.description}} 
          </div>
        </div>
      </div>
    </div>
	
	<!-- songlist -->
	<div class="songlix-box"  @showIdxPage="showpageId(idx)">
	  <li v-for="(item,index) in songdeial.tracks|| songdeial.songs" @click="add(item.id,item)" :key="item.index">
		  <span class="indexspan">{{index+1}}</span>
		  <span>
			  <p class="songtitle-p"> {{item.name}}</p>
			  <p class="songtitle-p1">
			  <span v-for="(item2,index2) in item.ar" :key="index2">{{item2.name}}-</span>
			  <span class="text-more">{{item.al.name}}</span>
			  </p>
		  </span>
	  </li>
	</div>

	<div class="audiobox" v-show="flag" :data="onesong">
	 <audio :src="onesong.url" autoplay ></audio>
	</div>
   <!-- 播放的页面的显示 默认是隐藏的 -->
  <div class="play-ying" v-if="isplayshow">
    <div class="plypading">
   <div class="playtitle">
      <van-icon name="arrow-left"  color="#fff" size="30px" @click="backgo"/>
     <div class="right-info">
         <div class="songs-title">{{palyerinfo.name}}</div>
      <div v-for="(item,index) in palyerinfo.ar" :key="index" style="font-size:0.3rem">{{item.name}}</div>
     </div>
   </div>
   <!-- 中间的圆 -->
   <div class="cicle-box">
     <div class="cicle-pole"></div>
     <div class="rotate">
       <div class="song-img">
         <img src="" alt="">
       </div>
     </div>
   </div>

    <div class="audio-ico">
      <van-icon name="like-o"  color="#bdc3c7"/>
      <van-icon name="down"  color="#bdc3c7"/>
      <van-icon name="notes-o" color="#bdc3c7" />
    </div>

   <!-- bar 播放的条 -->
   <div class="bar">
     <span>time</span>
     <div class="bar-bg">
       <div class="bar-larer"></div>
       <div class="point"></div>
     </div>
     <span></span>
   </div>
</div>
  </div>


  </div>
</template>

<script>
import Songlist from '../songlistPage/songlist'
import Seninority from '../../components/seninority/Seninority'

export default {
  data() {
    return {
    // 头部数据
    detailtoplist:[],
    //歌单数据
    songlist:[],
	  song:[],
	  songdeial:[],
	  onesong:[],
    flag:false,
    idx:'',
    isplayshow:false,
    palyerinfo:[]
    }
  },
  created() {
  this.getdetail()
      
  },
  components:{
  Songlist,
  Seninority
  },
  methods: {
    goback() {
      this.$router.go(-1);//返回上一层
    },
    async getdetail(){
    this.idx=this.$route.query.idx
    const {data:res}= await  this.$http.get(`top/list?idx=${this.idx}`)
    this.detailtoplist=res.playlist
    this.songlist=res.privileges
	  this.songdeial=res.playlist
    },
	async add(id,item){
  this.palyerinfo=item
  console.log(this.palyerinfo)
  this.isplayshow=!this.isplayshow
	const {data:res}=await	this.$http.get('song/url?id='+id)
	this.onesong=res.data[0]

	},
  showIdxPage(id){
    console.log(id)

  },
  backgo(){
    this.isplayshow=!this.isplayshow
  }
  }
}
</script>

<style lang="less" scoped>
.album_container {
    margin: 0;
    padding: 0;
  .top_container {
      width: 100%;
      height: 300px;
      background: #ee5253;
      padding: 0 !important;
    .top_bar {
      height: 50px;
      display: flex;
      align-items: center;
    }
   .top_info_Box{
       padding: 0px 10px;
       display: flex;
      .left-img{
          width: 135px;
          height: 135px;
          position: relative;
          .top-icon{
              color: #fff;
              position: absolute;
              top: 0;
              right: 0px;
              font-size: 11px;
              height: 15px;
              display: flex;
              align-items: center;
          } 
      }
      img{
      width: 100%;
      height: 100%;
      }
      .right-box{
          padding-left: 30px;
          flex: 1;
          .title-top{
              font-size: 20px;
              color: #fff;
              overflow: hidden;
              height: 28px;
              line-height: 28px;
          }
          .middle_box{
              color: #ccc;
              height: 52px;
              line-height: 52px;
              font-size: 14px;
              .van-icon{
                  vertical-align: middle;
              }
              .middle-text{
                  padding-left: 15px;
              }
          }
          .bottm-text{
                color: #ccc;
                font-size: 15px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
                width: 160px;
          }
      }
   }
  }
  .songlix-box{
	  padding: 6px 12px;
	  li{
		  height: 53px;
		  display: flex;
	      align-items: center;
		  .indexspan{
			  padding-right: 15px;
        color: #999;
        font-size: 16px;
		  }
		  .songtitle-p{
			  height: 20px;
			  font-size: 14px;
			  color: #333;
			  width: 300px;
			  white-space: nowrap;
			  overflow: hidden;
			  text-overflow: ellipsis;
		  }
		  .songtitle-p1{
			color: #dacdcd;
			height:20px;
			font-size: 12px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			width: 300px;
		  }
	  }
  }

  .play-ying{   
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #7f8c8d;
    z-index: 100;
    .plypading{
      margin: 0 0.26667rem;
    .playtitle{
      display: flex;
      margin: 0.3rem 0;
      align-items: center;
      .right-info{
        font-size: 0.36rem;
        margin-left: 0.1rem;
       .songs-title{
         color: #fff;
         line-height: 1.5;
       }
       .song-starsong{
         font-size: 0.34rem !important;
         color: #bdc3c7;
       }
      }
    }
    .cicle-box{
      position: relative;
      margin: 0.6rem 0;
      overflow: hidden;
      .cicle-pole{
        width: 2.5rem;
        background-image: url(https://s2.ax1x.com/2019/09/13/nrJyJP.png);
        background-size: 100% 100%;
        background-repeat: no-repeat;
        position: absolute;
        transform: translateX(-50%);
        padding-bottom: 3rem;
        left: 50%;
      }
      .rotate{
      display: flex;
      justify-content: center;
      height: 0;
      width: 80vw;
      margin: 1.4rem auto;
      padding-bottom: 80vw;
      background-image: url(https://s2.ax1x.com/2019/09/12/n0yYqK.md.png);
      background-size: 100% 100%;
      background-repeat: no-repeat;
      }
    }
    .audio-ico{
      display: flex;
      justify-content: space-around;
      align-items: center;
    }

    .bar{
      display: flex;
      margin: 0.6rem 0;
      align-items: center;
      color: #bdc3c7;
      font-size: 0.2rem;
      .bar-bg{
        width: 100%;
        margin: 0 0.16rem;
        height: 2px;
        background: #95a5a6;
        position: relative;
        .bar-larer{
         height: 2px;
         background-color: #ecf0f1;
        }
        .point{
         position: absolute;
         top: -3px;
         border-radius: 50%;
         width: 8px;
         height: 8px;
         background-color: #ecf0f1;
        }
      }
    }
  }
   }
}
</style>
